import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common'; 
import { TranslateModule } from '@ngx-translate/core';
import { IonicModule } from '@ionic/angular';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
import { RouterModule, Routes } from '@angular/router';
import { DetailComponent } from './detail/detail.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { ReturnSupplierComponent } from './return-supplier.component';
import { BillType10Module } from 'src/app/cafe/components/bill-type/bill-type-10/bill-type10.module';
import { AddComponent } from './add/add.component';
import { EditComponent } from './edit/edit.component';

const routes: Routes = [
  {
    path: "",
    component: ReturnSupplierComponent
  },
  {
    path: "add",
    component: AddComponent
  },
  {
    path: "detail",
    component: DetailComponent
  },{
    path:"edit-return-supplier",
    component: EditComponent
  }
]


@NgModule({
  declarations: [
    ReturnSupplierComponent,DetailComponent,AddComponent,EditComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule,
    ReactiveFormsModule,
    FormsModule,
    BillType10Module
  ]
})
export class ReturnSupplierModule { }
